import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';
import { Timestamp, scheduled } from 'rxjs';
import "moment-timezone";
import { EmployeesService } from '../services/employees.service';
import { ScheduleService } from '../services/schedule.service';
import { CombineLatestOperator } from 'rxjs/internal/observable/combineLatest';
// import { NgbModal, NgbTimepicker} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.sass']
})
export class HomepageComponent implements OnInit {

  TODAY = new Date();
  monday = new Date();
  tuesday = new Date();
  wednesday = new Date();
  thursday = new Date();
  friday = new Date();
  saturday = new Date();
  sunday = new Date();
  DAYS_OF_THE_WEEK = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday']
  employees = [{ id: null, name: null, monday: null, tuesday: null, wednesday: null, thursday: null, friday: null, saturday: null, sunday: null }];
  changingDates = false;
  editingSchedule = false;
  daySchedule = { employeeId: null, startTime: "", endTime: "" }

  constructor(
    private employeeService: EmployeesService,
    private scheduleService: ScheduleService,
  ) { }


  ngOnInit() {
    this.setThisWeek(this.TODAY);
  }

  setThisWeek(date) {
    if (!this.changingDates) {
      this.changingDates = true;
      let MONDAY = this.getMonday(date);
      // Moment is needed due to a weird bug with Date()
      this.monday = moment(MONDAY).toDate();
      this.tuesday = moment(MONDAY).add(1, 'days').toDate();
      this.wednesday = moment(MONDAY).add(2, 'days').toDate();
      this.thursday = moment(MONDAY).add(3, 'days').toDate();
      this.friday = moment(MONDAY).add(4, 'days').toDate();
      this.saturday = moment(MONDAY).add(5, 'days').toDate();
      this.sunday = moment(MONDAY).add(6, 'days').toDate();
      this.changingDates = false;
      this.getActiveEmployees()
    }
  }

  async getActiveEmployees() {
    this.employees = [{ id: null, name: null, monday: null, tuesday: null, wednesday: null, thursday: null, friday: null, saturday: null, sunday: null }];
    await this.employeeService.getAllActiveEmployees().toPromise().then(res => {
      this.employees = res as any;
    });
    for (let i = 0; i < this.employees.length; i++) {
      // set times as blank by default, used to stop errors when creating placeholders for editing
      this.employees[i].monday = { id: null, employeeId: this.employees[i].id, startTime: "", endTime: "" };
      this.employees[i].tuesday = { id: null, employeeId: this.employees[i].id, startTime: "", endTime: "" };
      this.employees[i].wednesday = { id: null, employeeId: this.employees[i].id, startTime: "", endTime: "" };
      this.employees[i].thursday = { id: null, employeeId: this.employees[i].id, startTime: "", endTime: "" };
      this.employees[i].friday = { id: null, employeeId: this.employees[i].id, startTime: "", endTime: "" };
      this.employees[i].saturday = { id: null, employeeId: this.employees[i].id, startTime: "", endTime: "" };
      this.employees[i].sunday = { id: null, employeeId: this.employees[i].id, startTime: "", endTime: "" };
      //get schedules and reassign them to something easy to display and manipulate on the frontend
      this.scheduleService.getAllScheduledByWeekByEmployee(moment(this.monday).format('YYYY-MM-DD'), moment(this.sunday).format('YYYY-MM-DD'), this.employees[i].id).subscribe(res => {
        for (let r = 0; r < res.length; r++) {
          // LT format looks like '1:00 PM'
          if (moment(res[r].startTime).format('dddd') === 'Monday') {
            this.employees[i].monday = { id: res[r].id, employeeId: this.employees[i].id, startTime: moment(res[r].startTime).format('LT'), endTime: moment(res[r].endTime).format('LT') };
          }
          if (moment(res[r].startTime).format('dddd') === 'Tuesday') {
            this.employees[i].tuesday = { id: res[r].id, employeeId: this.employees[i].id, startTime: moment(res[r].startTime).format('LT'), endTime: moment(res[r].endTime).format('LT') };
          }
          if (moment(res[r].startTime).format('dddd') === 'Wednesday') {
            this.employees[i].wednesday = { id: res[r].id, employeeId: this.employees[i].id, startTime: moment(res[r].startTime).format('LT'), endTime: moment(res[r].endTime).format('LT') };
          }
          if (moment(res[r].startTime).format('dddd') === 'Thursday') {
            this.employees[i].thursday = { id: res[r].id, employeeId: this.employees[i].id, startTime: moment(res[r].startTime).format('LT'), endTime: moment(res[r].endTime).format('LT') };
          }
          if (moment(res[r].startTime).format('dddd') === 'Friday') {
            this.employees[i].friday = { id: res[r].id, employeeId: this.employees[i].id, startTime: moment(res[r].startTime).format('LT'), endTime: moment(res[r].endTime).format('LT') };
          }
          if (moment(res[r].startTime).format('dddd') === 'Saturday') {
            this.employees[i].saturday = { id: res[r].id, employeeId: this.employees[i].id, startTime: moment(res[r].startTime).format('LT'), endTime: moment(res[r].endTime).format('LT') };
          }
          if (moment(res[r].startTime).format('dddd') === 'Sunday') {
            this.employees[i].sunday = { id: res[r].id, employeeId: this.employees[i].id, startTime: moment(res[r].startTime).format('LT'), endTime: moment(res[r].endTime).format('LT') };
          }
        }
      });
    }
    console.log(this.employees);
  }

  getMonday(date) {
    let day = date.getDay(),
      diff = date.getDate() - day + (day == 0 ? -6 : 1); // adjust when day is sunday
    return new Date(date.setDate(diff));
  }

  weekBack() {
    this.monday.setDate(this.monday.getDate() - 7);
    this.setThisWeek(this.monday);
  }

  weekAhead() {
    this.monday.setDate(this.monday.getDate() + 7);
    this.setThisWeek(this.monday);
  }

  toggleEdit() {
    this.changingDates = !this.changingDates;
  }

  undoEdit() {
    this.setThisWeek(this.monday);
    this.toggleEdit();
  }

  saveEdit() {
    debugger;
    for (let e = 0; e < this.employees.length; e++) {
      if (this.employees[e].monday.startTime != `` && this.employees[e].monday.endTime != ``) {
        const newStartDate = this.convertToHoursAndMinutes(this.employees[e].friday.startTime, 1);
        const newEndDate = this.convertToHoursAndMinutes(this.employees[e].friday.endTime, 1);
        if (this.employees[e].monday.id != null) {
          const updateSchedule = { id: this.employees[e].monday.id, employeeId: this.employees[e].monday.employeeId, startTime: newStartDate, endTime: newEndDate };
          this.scheduleService.updateScheduleById(updateSchedule).toPromise().then( res => {
            console.log(updateSchedule);
          });
        } else {
          const createSchedule = { employeeId: this.employees[e].monday.employeeId, startTime: newStartDate, endTime: newEndDate };
          this.scheduleService.createScheduleById(createSchedule).toPromise().then( res => {
            console.log(createSchedule);
          });
        }

      }
    }
    this.toggleEdit();
  }

  convertToHoursAndMinutes(time, day) {
    let dayOfTheWeek
    switch (day) {
      case 1:
        dayOfTheWeek = this.monday;
        break;
      case 2:
        dayOfTheWeek = this.tuesday;
        break;
      case 3:
        dayOfTheWeek = this.wednesday;
        break;
      case 4:
        dayOfTheWeek = this.thursday;
        break;
      case 5:
        dayOfTheWeek = this.friday;
        break;
      case 6:
        dayOfTheWeek = this.saturday;
        break;
      case 7:
        dayOfTheWeek = this.sunday;
        break;
    }
    time.replace(/\s/g, "");
    let hour = time.split(`:`)[0];
    if (time.substring(time.length - 2, time.length).toUpperCase() === `PM`) {
      hour = (parseInt(hour) + 12).toString();
    }
    let minutes = time.split(`:`)[1].substring(0, 2)
    // console.log(hour, minutes);
    return moment(`${(moment(dayOfTheWeek).zone('+0600').format('YYYY-MM-DD'))} ${hour}:${minutes}:00`, "YYYY-MM-DD H:mm").toDate();
    // return moment(dayOfTheWeek).setHours(hour).setMinutes(minutes).toDate();
  }
}
